.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin: 40px 0;
}

.skill {
    text-align: center;
}

.skill i {
    font-size: 48px;
    color: #007bff;
    /* Adjust the color to match your theme */
}

.skill h3 {
    margin-top: 10px;
    font-size: 20px;
}

.skill p {
    font-size: 16px;
}